.logo {
  width: 46px;
  height: 28px;
}

.logo__image {
  background-repeat: no-repeat;
  width: 100%;
  height: 100%;
}

.icon {
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
  width: 24px;
  height: 24px;
  display: -ms-flexbox;
  display: flex;
}

.icon__link {
  width: 100%;
  padding-bottom: 100%;
}

.icon--menu-opener {
  background-image: url("menu-opener.baec15cc.svg");
